var UserListTemplate = /** @class */ (function () {
    function UserListTemplate(user) {
        this._user = user;
    }
    UserListTemplate.prototype.buildHtmlElement = function () {
        var html = document.createElement('div');
        html.innerHTML =
            "\n\t\t\t<div>\n\t\t\t\t".concat(this._user.id(), "\n\t\t\t\t").concat(this._user.name(), "\n\t\t\t\t").concat(this._user.email(), "\n\t\t\t\t").concat(this._user.address(), "\n\t\t\t\t<a href=\"/user/").concat(this._user.id(), "/\">Edit</a>\n\t\t\t\t<input type=\"checkbox\" name=\"userDelete\" value=\"").concat(this._user.id(), "\">\n\t\t\t</div>\n\t\t\t");
        return html;
    };
    return UserListTemplate;
}());
export default UserListTemplate;
